<template>
    <div>
        <Spin v-if="loading" class="spinClass">
            <span><span class="dot">...</span></span>
        </Spin>
       <Tree :data="data5" :render="renderContent" class="demo-tree-render" v-else></Tree>
    </div>
</template>
<script>
    import event from "../../assets/js/event";
    export default {
        data () {
            return {
                modal1: false,
                loading:false,
                sortList:[],
                data5: [
                    {
                        id:'0',
                        label:'',
                        title: '',
                        expand: true,
                        render: (h, { root, node, data }) => {
                            return h('span', {
                                style: {
                                    display: 'inline-block',
                                    width: '100%'
                                }
                            }, [
                                h('span', [
                                    h('Icon', {
                                        props: {
                                            type: 'ios-folder-outline',
                                        },
                                        style: {
                                            marginRight: '8px'
                                        }
                                    }),
                                    h('span', data.title)
                                ]),
                                //
                                h('span', {
                                    domProps: {
                                        title: ''
                                    },
                                    style: {
                                        display: 'inline-block',
                                        float: 'right',
                                        marginRight: '40px'
                                    },
                                    directives:[
                                        {
                                            name:'auth',
                                            value:'view_menu',
                                        }
                                    ]
                                }, [
                                    h('Button', {
                                        props: Object.assign({}, this.buttonProps, {
                                            icon: 'ios-refresh',
                                            type: 'primary',
                                        }),
                                        style: {
                                            width: '100px'
                                        },
                                        on: {
                                            click: () => {
                                                this.getMenuList();
                                                //this.$emit('addMenuPage',1,root,data);//
                                            }
                                        }
                                    })
                                ]),
                                //
                                h('span', {
                                    domProps: {
                                        title: ''
                                    },
                                    style: {
                                        display: 'inline-block',
                                        float: 'right',
                                        marginRight: '10px'
                                    }
                                }, [
                                    h('Button', {
                                        props: Object.assign({}, this.buttonProps, {
                                            icon: 'ios-add',
                                            type: 'primary',
                                        }),
                                        style: {
                                            width: '98px'
                                        },
                                        on: {
                                            click: () => {
                                                this.$emit('addMenuPage',1,root,data);//
                                            }
                                        },
                                        directives:[
                                            {
                                                name:'auth',
                                                value:'add_menu',
                                            }
                                        ]
                                    })
                                ])
                            ]);
                        },
                        children: []
                    }
                ],
                buttonProps: {
                    type: 'default',
                    size: 'small',
                }
            }
        },
        mounted() {
            this.getMenuList();
        },
        methods: {
            renderContent (h, { root, node, data }) {
                return h('span', {
                        style: {
                            display: 'inline-block',
                            width: '100%'
                        }
                    },
                    [
                        //
                        h('span', [
                            h('Icon', {
                                props: {
                                    type: data.type == 2 ? 'ios-folder-outline' :'ios-paper-outline'
                                },
                                style: {
                                    marginRight: '8px'
                                }
                            }),
                            h('span', data.title)
                        ]),
                        h('span', {
                                style: {
                                    display: 'inline-block',
                                    float: 'right',
                                    marginRight: '32px'
                                }
                            },
                            //
                            [
                                h('Button', {
                                    domProps: {
                                        title: ''
                                    },
                                    props: Object.assign({}, this.buttonProps, {
                                        icon: 'ios-add'
                                    }),
                                    style: {
                                        marginRight: '8px'
                                    },
                                    on: {
                                        click: () => {
                                            this.$emit('addMenuPage',1,root,data);//
                                        }
                                    },
                                    directives:[
                                        {
                                            name:'auth',
                                            value:'add_menu',
                                        }
                                    ]
                                }),
                                //
                                h('Button', {
                                    domProps: {
                                        title: '1',
                                    },
                                    props: Object.assign({}, this.buttonProps, {
                                        icon: 'ios-link-outline',
                                        disabled:data.type == 2 ?true:false
                                    }),
                                    style: {
                                        marginRight: '8px'
                                    },
                                    on: {
                                        click: () => {
                                            this.$emit('addMenuPage',3,root,data);//
                                        }
                                    },
                                    directives:[
                                        {
                                            name:'auth',
                                            value:'view_permission',
                                        }
                                    ],
                                }),
                                //
                                h('Button', {
                                    domProps: {
                                        title: ''
                                    },
                                    props: Object.assign({}, this.buttonProps, {
                                        icon: 'ios-create-outline'
                                    }),
                                    style: {
                                        marginRight: '8px'
                                    },
                                    on: {
                                        click: () => {
                                            this.$emit('addMenuPage',2,root,data);//
                                        }
                                    },
                                    directives:[
                                        {
                                            name:'auth',
                                            value:'edit_menu',
                                        }
                                    ]
                                }),
                                //
                                h('Poptip', {
                                    props: {
                                        placement: 'right-start',
                                        confirm: true,
                                        transfer: true,
                                        title: '',
                                    },
                                    on: {
                                        'on-ok': () => {
                                            this.remove(root, node, data)//
                                        },
                                        'on-cancel': () => {
                                        }
                                    }
                                }, [
                                    h('Button', {
                                        domProps: {
                                            title: ''
                                        },
                                        props: Object.assign({}, this.buttonProps, {
                                            icon: 'ios-trash-outline'
                                        }),
                                        style: {
                                            marginRight: '8px'
                                        },
                                        directives:[
                                            {
                                                name:'auth',
                                                value:'del_menu',
                                            }
                                        ],
                                    })
                                ]),
                                //
                                h('InputNumber', {
                                    domProps: {
                                        title: ''
                                    },
                                    props: {
                                        size: 'small',
                                        value:parseInt(data.sort),
                                        min:1
                                    },
                                    style: {
                                        marginRight: '8px'
                                    },
                                    on: {
                                        'on-change': (sortVal) => {
                                            //data.sort = sortVal;
                                            this.updateMenuSort(data,sortVal);//
                                        }
                                    },
                                    directives:[
                                        {
                                            name:'auth',
                                            value:'sort_menu',
                                        }
                                    ]
                                }),
                            ])
                    ]);
            },
            //
            remove (root, node, data) {
                    const that =this;
                    const id = node.node.id;
                    let successCallback = function(res){
                        that.$Message.info({
                            content:'',
                            onClose:function () {
                                const parentKey = root.find(el => el === node).parent;
                                const parent = root.find(el => el.nodeKey === parentKey).node;
                                const index = parent.children.indexOf(data);
                                parent.children.splice(index, 1);
                            }
                        })
                    }
                    let failCallback = function(res){
                        that.$Message.error(res.data.message);
                    }
                    that.HTTPJS.post(that.HTTPURL.SYSTEM_SEETING.MENU.DELETE_MENU,{id:id},successCallback,failCallback);
            },
            //
            getMenuList(){
                const that =this;
                that.loading = true;
                    let successCallback = function(res){
                        that.data5[0].children = res.data.data;
                        that.loading = false;
                    }
                    let failCallback = function(res){
                        that.data5[0].children = [];
                        that.loading = false;
                    }
                    let otherCallback = function(res){
                        that.data5[0].children = [];
                        that.loading = false;
                    }
                    that.HTTPJS.get(that.HTTPURL.SYSTEM_SEETING.MENU.LIST,'',successCallback,failCallback,otherCallback);
            },
            //
            updateMenuSort(data,sortVal){
                const that = this;
                let successCallback = function(res){
                    that.$Message.info({
                        content:'',
                        onClose:function () {
                            data.sort = sortVal
                        }
                    })
                }
                let failCallback = function(res){
                    that.$Message.error({
                        content:'',
                    })
                }
                that.HTTPJS.post(that.HTTPURL.SYSTEM_SEETING.MENU.SORT_MENU,{id:data.id,sortVal:sortVal},successCallback,failCallback);
            }
        }
    }
</script>
<style>
    .demo-tree-render .ivu-tree-title{
        width: 100%;
    }
</style>