<template>
    <div>
        <Spin size="large" fix v-if="loading"></Spin>
        <Card :bordered="false" dis-hover>
            <p slot="title">
                
            </p>
            <Alert type="warning" show-icon>
                
                <template slot="desc">
                    <p>1</p>
                </template>
            </Alert>
            <Card :dis-hover="true" :bordered="true" style="margin-bottom: 10px;" v-for="(item, index) in tabs.items" :key="index" >
                <p slot="title">{{item.title}}</p>
                <p slot="extra">
                    <ButtonGroup size="small">
                        <Button icon="ios-create-outline" type="dashed"@click="showEditPermissionModel(1,index)" title=""></Button>
                        <Poptip
                                :transfer="true"
                                confirm
                                title="?"
                                @on-ok="deleteTabs(index)">
                            <Button icon="ios-trash-outline" type="dashed" size="small" title=""></Button>
                        </Poptip>
                    </ButtonGroup>
                </p>
                <p style="display: inline-block;" >
                    <Input v-model="item1.url"
                           style="width: 250px;float: left;margin-right: 10px;margin-bottom: 10px;"
                           placeholder="model / controller / action"
                           v-for="(item1, index1) in item.pagePermissionItem" :key="index1">
                        <span slot="append"  @click="removeUrlItem(index,index1)" title=""><Icon type="md-close" /></span>
                    </Input>
                    <Button icon="ios-add" type="dashed" size="default" @click="addUrlItem(index)"></Button>
                </p>
            </Card>
            <Button type="dashed" icon="md-add" long class="add-content-btn" @click="addTasItem" style="margin-bottom: 10px;"size="large"></Button>
            <Button type="primary" long class="add-content-btn" @click="saveData" :loading="tabs.loadning" style="margin-bottom: 10px;" size="large"></Button>
        </Card>
        <!---->
        <Modal v-model="editPermissionModel.show"
               :closable="false"
               :mask-closable="false"
               title="">
            <Form ref="formValidate" :model="editPermissionModel.formInline" :rules="editPermissionModel.ruleInline" :label-width="95">
                <FormItem label="" prop="title">
                    <Input type="text" v-model="editPermissionModel.formInline.title" maxlength="10" placeholder="10"></Input>
                </FormItem>
                <FormItem label="" prop="identification">
                    <Input type="text" v-model="editPermissionModel.formInline.identification" maxlength="20" placeholder="20"></Input>
                </FormItem>
            </Form>
            <div slot="footer">
                <Button  @click="showEditPermissionModel(2,'')"></Button>
                <Button type="primary" :loading = 'editPermissionModel.loading'@click="editPermission('formValidate')"></Button>
            </div>
        </Modal>

        <!---->
    </div>
</template>
<script>
    import handleMenu from "../../assets/js/handleMenu";

    export default {
        data () {
            const validateEnglish = (rule, value, callback) => {
                if (value.trim() === '') {
                    return callback(new Error(''))
                } else if (/[^\a-\z\A-\Z_]/g.test(value)) {
                    return callback(new Error(''))
                } else {
                    callback()
                }
            };
            return {
                tabs: {
                    loadning:false,
                    loadingText:'',
                    items:[],
                },
                loading:false,
                treeData:{},
                editPermissionModel:{
                    show:false,
                    loading:false,
                    index:'',
                    formInline:{
                        identification:'',
                        title:'',
                    },
                    ruleInline:{
                        title: [
                            { required: true, message: '', trigger: 'blur' },
                            { type: 'string', max: 10, message: '10', trigger: 'blur' }
                        ],
                        identification: [
                            { required: true, message: '', trigger: 'blur' },
                            { type: 'string', max: 20, message: '20', trigger: 'blur' },
                            { required: true, trigger: 'blur', validator: validateEnglish }
                        ],
                    },

                }
            }
        },
        methods: {
            addUrlItem (index) {
                this.tabs.items[index].pagePermissionItem.push({url:''});
            },
            removeUrlItem (index, index1) {
                this.tabs.items[index].pagePermissionItem.splice(index1, 1);
            },
            deleteTabs (index) {
                this.tabs.items.splice(index, 1);
            },
            addTasItem(){
                this.tabs.items.push( {
                    id:'',
                    title:'',
                    pagePermissionItem:[]
                });
            },
            showEditPermissionModel(type,index){//type == 12
                const that =this;
                if(type == 1){
                    that.editPermissionModel.show = true;
                    that.editPermissionModel.index = index;
                    that.editPermissionModel.formInline.title = that.tabs.items[index].title;
                    that.editPermissionModel.formInline.identification = that.tabs.items[index].identification;
                }else{
                    that.editPermissionModel.show = false;
                }
            },
            //
            editPermission (name) {
                const that =this;
                that.$refs[name].validate((valid,errors) => {
                    if (valid) {
                        that.tabs.items[that.editPermissionModel.index].title = that.editPermissionModel.formInline.title;
                        that.tabs.items[that.editPermissionModel.index].identification = that.editPermissionModel.formInline.identification;
                        that.showEditPermissionModel(2,'');
                    }
                })
                // this.$Modal.confirm({
                //     render: (h) => {
                //         return h('Input', {
                //             props: {
                //                 value: this.tabs.items[index].title,
                //                 autofocus: true,
                //                 placeholder: '...'
                //             },
                //             on: {
                //                 input: (val) => {
                //                     if(val != ''){
                //                         this.tabs.items[index].title = val;
                //                     }
                //                 }
                //             }
                //         })
                //     }
                // })
            },
            //
            saveData(){
                const parms = {
                    menuId:this.treeData.id,
                    data:this.tabs.items
                };
                //console.log(parms)
                const that =this;
                that.tabs.loadingText = '...';
                that.tabs.loadning = true;
                let successCallback = function(res){
                    that.$Message.info({
                        content:'',
                        onClose:function () {
                            that.tabs.loadingText = '';
                            that.tabs.loadning = false;
                        }
                    })
                }
                let failCallback = function(res){
                    that.$Message.error({
                        content:res.data.message,
                        onClose:function () {
                            that.tabs.loadingText = '';
                            that.tabs.loadning = false;
                        }
                    })
                }
                let otherCallback = function(res){
                    that.tabs.loadingText = '';
                    that.tabs.loadning = false;
                }
                that.HTTPJS.post(that.HTTPURL.SYSTEM_SEETING.MENU.ADD_PAGE_PERMISSION,parms,successCallback,failCallback,otherCallback);

            },
            //
            getPagePermissionList(){
                const that =this;
                that.loading = true;
                let successCallback = function(res){
                    that.tabs.items = res.data.data;
                    that.loading = false;
                }
                let failCallback = function(res){
                    that.tabs.items = [];
                    that.loading = false;
                }
                let otherCallback = function(res){
                    that.tabs.items = [];
                    that.loading = false;
                }

                that.HTTPJS.get(that.HTTPURL.SYSTEM_SEETING.MENU.GET_PAGE_PERMISSION_ITEM,{menuId:this.treeData.id},successCallback,failCallback,otherCallback);
            },
            ////tree
            setParent(parentList,parentData){
                this.treeData = parentData;
                this.getPagePermissionList();
                // if(parentData.pagePermission && parentData.pagePermission.list.length >= 1){
                //     this.formDynamic.items = parentData.pagePermission.list;
                // }else{
                //     this.formDynamic.items = [];
                // }
                //console.log(this.treeData)
            },
        }
    }
</script>
