<template>
    <div>
        <Form ref="formInline" :label-width="250"  :model="formInline" :rules="ruleInline" label-position="left" :hide-required-mark="true">
            <Card :bordered="false" dis-hover>
                <p slot="title">
                    {{info}}
                </p>
                <Alert type="warning" show-icon>
                    
                    <template slot="desc">
                        <p>1""vue/vueTempate/src/views//.../.vue</p>
                        <p>2vue</p>
                    </template>
                </Alert>
                <FormItem label="" prop="parent_id">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info">3;</Col>
                        </Row>
                    </div>
                    <treeselect v-model="formInline.parent_id"
                                placeholder=""
                                @select="selectParent"
                                @input="changeParentId"
                                :clearable="false"
                                :defaultExpandLevel = "Infinity"
                                :normalizer="parentList.normalizer"
                                noOptionsText=""
                                :options="parentList.options" style="width:319px;"/>
                </FormItem>
                <FormItem label="" prop="title">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info">10</Col>
                        </Row>
                    </div>
                    <Input type="text" v-model="formInline.title" placeholder="" style="width: 319px"></Input>
                </FormItem>
                <FormItem label="" prop="icon">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info"></Col>
                        </Row>
                    </div>
                    <Input type="text"
                           v-model="formInline.icon"
                           :readonly="true"
                           search
                           @on-focus="showSelectIconModel"
                           enter-button=""
                           style="width: 319px"></Input>
                </FormItem>
                <FormItem label="URL" prop="href">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info">20</Col>
                        </Row>
                    </div>
                    <Input type="text" v-model="formInline.href" placeholder="" style="width: 319px"></Input>
                </FormItem>
                <FormItem label="" prop="type">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info"></Col>
                        </Row>
                    </div>
                    <RadioGroup v-model="formInline.type" @on-change="selectMenuType">
                        <Radio label="2"></Radio>
                        <Radio label="1"></Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem label="" prop="status">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info"></Col>
                        </Row>
                    </div>
                    <RadioGroup v-model="formInline.status">
                        <Radio label="1"></Radio>
                        <Radio label="2"></Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem label="" prop="create_template">
                    <div slot="label">
                        <Row>
                            <Col span="24" class="label-required-name"></Col>
                            <Col span="24" class="label-info">""vue;
                                <span style="color: red;"></span></Col>
                        </Row>
                    </div>
                    <RadioGroup v-model="formInline.create_template">
                        <Radio label="1" :disabled="formInline.type == 2"></Radio>
                        <Radio label="2" :disabled="formInline.type == 2"></Radio>
                    </RadioGroup>
                </FormItem>
            </Card>
<!--            <div class="addBtn">-->
<!--                <div>-->
                    <Button type="primary" :loading="loading" @click="addMenu('formInline')" size="large">{{info}}</Button>
<!--                </div>-->
<!--            </div>-->
        </Form>
        <!---->
        <menu-icon ref="menuIcon" @getIcon="getIcon"></menu-icon>
    </div>
</template>

<script>
    import menuIcon from './Icon'
    import event from "../../assets/js/event"
    import Treeselect from '@riophae/vue-treeselect'
    import '@riophae/vue-treeselect/dist/vue-treeselect.css'
    export default {
        components: {
            'menu-icon':menuIcon,
            'event':event,
            Treeselect
        },
        data() {
            return {
                treeData:{},
                parentList:{
                    options: [],
                    normalizer(node){
                        //children=[]children respectively
                        if(node.children && !node.children.length){
                            delete node.children;
                        }
                    }
                },
                loading:false,
                info:'',
                formInline:{
                    id:'',
                    is_add:1,//12
                    parent_id:'0',
                    title:'',
                    icon:'',
                    href:'',
                    type:'2',
                    status:'1',
                    create_template:'2'
                },
                ruleInline:{
                    parent_id: [
                        { type: 'string', required: true, message: '', trigger: 'input' }
                    ],
                    title: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', max: 10, message: '10', trigger: 'blur' }
                    ],
                    href: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', max: 50, message: '50', trigger: 'blur' },
                    ],
                },
            }
        },
        methods: {
            selectMenuType(value){
                if(value == 1){
                    this.formInline.create_template = '1';
                }else{
                    this.formInline.create_template = '2';
                }
            },
            showSelectIconModel(){
                this.$refs.menuIcon.showMoel(true)
            },
            getIcon(icon){
                this.formInline.icon = icon
                this.$refs.menuIcon.showMoel(false)
            },
            //
            addMenu(name){
                const that =this;
                // console.log(123)
                // this.$emit('getMenuListOne','');////
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        that.loading = true;
                        let successCallback = function(res){
                            that.$Message.info({
                                content:that.info+'',
                                onClose:function () {
                                    that.loading = false;
                                    that.$emit('getMenuList');//
                                    if(that.formInline.is_add == 1){//
                                        const children = that.treeData.children || [];
                                        children.push({
                                            id:res.data.data.id.toString(),
                                            type: that.formInline.type,
                                            label: that.formInline.title,
                                            title: that.formInline.title,
                                            sort: 1,
                                            expand: true
                                        });
                                        that.$set(that.treeData, 'children', children);
                                        that.$refs['formInline'].resetFields();
                                    }else{//
                                        that.treeData.type = that.formInline.type;
                                        that.treeData.label = that.formInline.title;
                                        that.treeData.title = that.formInline.title;
                                        that.treeData.sort = 1;
                                    }
                                }
                            })
                        }
                        let failCallback = function(res){
                            that.$Message.error({
                                content:res.data.message,
                                onClose:function () {
                                    that.loading = false;
                                }
                            })
                        }
                        let otherCallback = function(res){
                            that.loading = false;
                        }
                        that.HTTPJS.post(that.HTTPURL.SYSTEM_SEETING.MENU.ADD_OR_EDIT,that.formInline,successCallback,failCallback,otherCallback);
                    }
                })
            },
            //
            changeParentId(){
                this.$nextTick(()=>{
                    this.$refs["formInline"].validateField('parent_id');
                })
            },
            //
            selectParent(node, instanceId){
                this.$refs["formInline"].validateField('parent_id');
            },
            //
            setParent(parentList,parentData){
                this.$refs['formInline'].resetFields();
                this.info='';
                this.formInline.id     = '';
                this.formInline.is_add = 1;
                this.treeData = parentData;//tree
                this.parentList.options = [];
                this.parentList.options.push(parentList[0].node);
                this.formInline.parent_id = parentData.id;
                this.changeParentId();
            },
            //
            getMenuInfo(parentList,parentData){
                const that =this;
                const id = parentData.id;
                this.info='';
                let successCallback = function(res){
                    that.formInline = res.data.data;
                    that.formInline.id     = id;
                    that.formInline.is_add = 2;
                }
                that.HTTPJS.get(that.HTTPURL.SYSTEM_SEETING.MENU.GET_ONLY_LMANU,{id:id},successCallback);
            },
        }
    }
</script>
